<template>
  <v-app>
    <v-navigation-drawer
      app
      temporary
      v-model="drawer"
    >
      <v-list>

        <v-list-group
          prepend-icon="filter"
          value="true"
        >
          <v-list-tile slot="activator">
            <v-list-tile-title>Фильтр</v-list-tile-title>
          </v-list-tile>

          <v-list-group
            no-action
            sub-group
            value="true"
          >
            <v-list-tile slot="activator">
              <v-list-tile-title>Admin</v-list-tile-title>
            </v-list-tile>

            <v-list-tile

              @click=""
            >
              <v-list-tile-title></v-list-tile-title>
              <v-list-tile-action>
                <v-icon></v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>


        </v-list-group>

        <v-list-tile
          v-for="link of links"
          :key="link.title"
          :to="link.url"
        >
          <v-list-tile-action>
            <v-icon>{{link.icon}}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title v-text="link.title"></v-list-tile-title>
          </v-list-tile-content>

        </v-list-tile>


        <v-list-tile slot="activator">
          <v-list-tile-title>Users</v-list-tile-title>
        </v-list-tile>

                <v-list-tile
                  @click="onLogout"
                  v-if="isUserLoggedIn"
                >
                  <v-list-tile-action>
                    <v-icon>exit_to_app</v-icon>
                  </v-list-tile-action>

                  <v-list-tile-content>
                    <v-list-tile-title v-text="'Выйти'"></v-list-tile-title>
                  </v-list-tile-content>

                </v-list-tile>

      </v-list>
    </v-navigation-drawer>

    <v-toolbar app dark absolute clipped-left color="primary">

      <v-toolbar-side-icon
        class="hidden-md-and-up"
        @click="drawer = !drawer"
      ></v-toolbar-side-icon>

      <v-toolbar-title>
        <router-link to="/" tag="span" class="pointer">Конфигуратор серверов ТАЛМЕР</router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">

        <v-btn
          v-for="link in links"
          :key="link.title"
          :to="link.url"
          flat
        >
          <v-icon left>{{link.icon}}</v-icon>
          {{link.title}}
          <v-btn color="red" fab small dark v-if="link.count">
            {{link.count}}
          </v-btn>
        </v-btn>

                <v-btn
                  @click="onLogout"
                  flat
                  v-if="isUserLoggedIn"
                >
                  <v-icon left>exit_to_app</v-icon>
                  Выйти
                </v-btn>

      </v-toolbar-items>

    </v-toolbar>

    <v-content>
      <router-view></router-view>
    </v-content>

    <template v-if="error">

      <v-snackbar
        :multi-line="true"
        :timeout="5000"
        @input="closeError"
        @value="true"
        color="error"
      >
        {{error}}
        <v-btn
          dark
          flat
          @click.native="closeError"
        >
          Close
        </v-btn>
      </v-snackbar>
    </template>

  </v-app>
</template>

<script>
  export default {
    name: 'App',
    data() {
      return {
        drawer: false,

      }
    },
    methods: {
      closeError() {
        this.$store.dispatch('clearError')
      },
      onLogout() {
        this.$store.dispatch('logoutUser');
        this.$router.push('/')
      }
    },
    computed: {
      getTovars() {
        return this.$store.getters.tovar
      },
      error() {
        return this.$store.getters.error
      },
      isUserLoggedIn() {
        return this.$store.getters.isUserLoggedIn
      },
      links() {
        if (this.isUserLoggedIn) {
          return [
            {title: 'Личный кабинет', icon: 'perm_identity', url: '/orders',count:''},
            {title: 'Корзина', icon: 'shopping_cart', url: '/list',count:`${this.getTovars.length}`},
            // {title: 'Логин', icon: 'lock', url: '/login'},
            // {title: 'Регистрация', icon: 'face', url: '/registration'},
          ]
        } else {
          return [
            {title: 'Логин', icon: 'lock', url: '/login',count:''},
            {title: 'Регистрация', icon: 'face', url: '/registration',count:''},
          ]
        }
      }
    },
    created(){
      // try {
      //   this.$store.dispatch('getTovarsJson', this.$store.getters.user.id);
      // } catch (e) {
      //   console.log(e)
      // }
    },
    props: {
      source: String
    }
  }
</script>

<style>
  .pointer {
    cursor: pointer;
  }

  @import url("https://fonts.googleapis.com/css?family=Fira+Sans'");
  .application {
    font-family: 'Fira Sans', sans-serif;
  }

  .navigation-drawer__border {
    display: none;
  }


</style>



