import axios from 'axios'


export default {
  state: {
    tovarsJson: '',
    OrdersClientJson: '',
  },
  mutations: {

    loadTovarsJson(state, payload) {
      state.tovarsJson = payload
    },
    loadOrdersClientJson(state, payload) {
      state.OrdersClientJson = payload
    },


  },
  actions: {
    async createOrder({commit}, tovar) {
      try {
        await axios.post('http://10.100.0.108:8900/api/cart', tovar)
          .then(response =>
            console.log(response))
      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
    async getTovarsJson({commit}, id) {
      commit('setLoading', true);
      try {
        await axios.get('http://10.100.0.108:8900/api/cart/' + id)
          .then(response => {
            commit('loadTovarsJson', response.data);
            commit('setLoading', false)
          })
      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
    async deleteTovar({commit}, id) {
      try {
        await axios.delete('http://10.100.0.108:8900/api/cart/' + id)
          .then(response =>
            console.log(response))

      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
    async sendOrders({commit}, data) {
      try {
        await axios.post('http://10.100.0.108:8900/api/order', data)
          .then(response =>
            console.log(response))
      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
    async getOrdersClientJson({commit}, id) {
      commit('setLoading', true);
      try {
        await axios.get('http://10.100.0.108:8900/api/orders/' + id)
          .then(response => {
            commit('loadOrdersClientJson', response.data);
            commit('setLoading', false)
          })
      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
  },
  getters: {
    tovar(state) {
      return state.tovarsJson
    },
    ordersClient(state) {
      return state.loadOrdersClientJson
    },


  }
}



