import axios from 'axios'

export default {
  state: {
    categoryJson: '',
    oneCategoryJson: '',
    oneServerJson: '',
    disksJson: '',
    proccesorsJson:'',
    filtersJson:'',
    warrantyProgramJson:'',
    osJson:'',
    networkJson:'',
    hbaJson:'',
    disksValueJson:'',
    superDomValueJson:'',
    raidJson:'',
    transiverJson:'',
    serversJson:'',
  },
  mutations: {
    loadCategory(state, payload) {
      state.categoryJson = payload
    },
    loadOneCategory(state, payload) {
      state.oneCategoryJson = payload
    },
    loadOneServer(state, payload) {
      state.oneServerJson = payload
    },
    loadDisks(state, payload) {
      state.disksJson = payload
    },
    loadProcessors(state, payload) {
      state.proccesorsJson = payload
    },
    loadFilters(state, payload) {
      state.filtersJson = payload
    },
    loadWarrantyProgram(state, payload) {
      state.warrantyProgramJson = payload
    },
    loadOsJson(state, payload) {
      state.osJson = payload
    },
    loadNetworkJson(state, payload) {
      state.networkJson = payload
    },
    loadHbaJson(state, payload) {
      state.hbaJson = payload
    },
    loadDisksValueJson(state, payload) {
      state.disksValueJson = payload
    },
    loadSuperDomValueJson(state, payload) {
      state.superDomValueJson = payload
    },
    loadRaidJson(state, payload) {
      state.raidJson = payload
    },
    loadTransiverJson(state, payload) {
      state.transiverJson = payload
    },
    loadServersJson(state, payload) {
      state.serversJson = payload
    },


  },
  actions: {
    async adAllJson({commit},_id) {
      commit('setLoading', true);
      try {
        await axios.get('http://10.100.0.108:8900/api/server/' + _id)
          .then(response => {
            commit('loadOneServer', response.data);

            axios.get('http://10.100.0.108:8900/api/proccesorsQname/?attributes.value=' + response.data.pr_socet)
              .then(response => {
                commit('loadServersJson', response.data);
              })
          });
        await axios.get('http://10.100.0.108:8900/api/disksAll')
          .then(response => {
            commit('loadDisks', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/disksAll')
          .then(response => {
            commit('loadDisks', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/proccesorsAll')
          .then(response => {
            commit('loadProcessors', response.data);

          });
        await axios.get('http://10.100.0.108:8900/api/wpDisp ')
          .then(response => {
            commit('loadWarrantyProgram', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/trsDisp')
          .then(response => {
            commit('loadTransiverJson', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/rcsDisp')
          .then(response => {
            commit('loadRaidJson', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/dvDOMDisp')
          .then(response => {
            commit('loadSuperDomValueJson', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/dvDisp')
          .then(response => {
            commit('loadDisksValueJson', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/hbaDisp')
          .then(response => {
            commit('loadHbaJson', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/lpvDisp')
          .then(response => {
            commit('loadNetworkJson', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/osDisp ')
          .then(response => {
            commit('loadOsJson', response.data);
            commit('setLoading', false)
          })
      } catch (e) {
        commit('setError', e.message);
        throw e
      }

    },

    async getCategoryJson({commit}) {
      commit('setLoading', true);
      try {
        await axios.get('http://10.100.0.108:8900/api/category')
          .then(response => {
            commit('loadCategory', response.data);
            commit('setLoading', false)
          })
      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
    async getOneCategoryJson({commit}, key) {
      commit('setLoading', true);
      try {
        await axios.get('http://10.100.0.108:8900/api/' + key + 'min')
          .then(response => {
            commit('loadOneCategory', response.data);
          });
        await axios.get('http://10.100.0.108:8900/api/filters')
          .then(response => {
            commit('loadFilters', response.data);
            commit('setLoading', false)
          })
      } catch (e) {
        commit('setError', e.message);
        throw e
      }
    },
      },
  getters: {
    category(state) {
      let one = state.categoryJson[0];
      return one['column']
    },
    oneCategory(state) {
      // console.log(state.oneCategoryJson);
      return state.oneCategoryJson
    },
    adById(state) {
      // console.log(state.oneServerJson);
      return state.oneServerJson
    },
    disk(state) {
      return state.disksJson
    },
    lineproc(state){
      return state.proccesorsJson
    },
    filters(state){
      return state.filtersJson
    },
    warrantyProgram(state){
      return state.warrantyProgramJson
    },
    os(state){
      return state.osJson
    },
    network(state){
      return state.networkJson
    },
    hba(state){
      return state.hbaJson
    },
    disksValue(state){
      return state.disksValueJson
    },
    superDomValue(state){
      return state.superDomValueJson
    },
    raid(state){
      return state.raidJson
    },
    transiver(state){
      return state.transiverJson
    },
    server(state){
      return state.serversJson
    }
  }
}



