<template>
  <v-container fluid grid-list-md>
    <v-layout v-if="loading">
      <v-flex xs12 class="text-xs-center pt-5" v-if="loading">
        <v-progress-circular indeterminate :size="100" :width="4" color="purple">
        </v-progress-circular>
      </v-flex>
    </v-layout>

    <v-layout row wrap v-else-if="!loading">
      <v-flex xs12>
        <h1 class="text-xs-center display-1 mb-2">{{typeCategory[this.id]}}</h1>
      </v-flex>

      <v-flex d-flex xs3 child-flex class="mt-4">
        <v-flex color="green lighten-2" dark>
          <details>
            <summary>Модельный ряд</summary>
            <v-container fluid class="pb-0">
              <v-checkbox v-for="model in filters['processors_Model']"
                          :label="model"
                          :value='model'
                          v-model="adsVolumeProc"
                          :key="model"
                          height="1px"
                          class="mt-0 pb-0"></v-checkbox>
            </v-container>
          </details>

          <details>
            <summary>Линейка процессоров</summary>
            <v-container fluid class="pb-0">
              <v-checkbox v-for="proc in filters['processors_Lines']"
                          :label="proc"
                          :value='proc'
                          v-model="lineProcArr"
                          :key="proc"
                          height="1px"
                          class="mt-0 pb-0"></v-checkbox>
            </v-container>
          </details>

          <details>
            <summary>Максимальный Объем оперативной памяти</summary>
            <v-card-text>
              <v-slider
                v-model="asdMemorys"
                :tick-labels="filters['max_Memory']"
                :max="filters['max_Memory'] == 'undefined'? 0 : filters['max_Memory'].length-1"
                step="1"
                ticks="always"
                tick-size="3"
              ></v-slider>

            </v-card-text>
          </details>

          <details>
            <summary>Дисковые корзины</summary>
            <v-container fluid class="pb-0">
              <v-checkbox v-for="proc in filters['disks_Array']"
                          :label="proc"
                          :value='proc'
                          v-model="lineProcArr"
                          :key="proc"
                          height="1px"
                          class="mt-0 pb-0"></v-checkbox>
            </v-container>
            <ul>
              <li style="list-style-type: none">
                <!--                  <template v-for="value in hotSwaps['value']">-->
                <!--                    <v-layout row>-->
                <!--                      <v-checkbox :label="value"-->
                <!--                                  :value='value'-->
                <!--                                  v-model="hotSwap"></v-checkbox>-->
                <!--                      <v-slider-->
                <!--                        v-model="hotSwapObj[value]"-->
                <!--                        :tick-labels="hotSwaps['volume']"-->
                <!--                        :max="hotSwaps['volume'].length-1"-->
                <!--                        step="1"-->
                <!--                        ticks="always"-->
                <!--                        tick-size="3"-->
                <!--                        v-if="hotSwap.includes(value)"-->
                <!--                      ></v-slider>-->
                <!--                    </v-layout>-->
                <!--                  </template>-->
              </li>
            </ul>
          </details>

          <details>
            <summary>Форм-фактор сервера</summary>
            <v-container fluid class="pb-0">
              <v-checkbox v-for="factor in filters['formFactor']"
                          :label="factor"
                          :value='factor'
                          v-model="formFactor"
                          height="1px"
                          :key="factor"
                          class="mt-0 pb-0"
              ></v-checkbox>
            </v-container>
          </details>

          <details>
            <summary>Сетевой контроллер</summary>
            <v-container fluid class="pb-0">
              <v-checkbox v-for="controller in filters['lan_controller']"
                          :label="controller"
                          :value='controller'
                          v-model="network"
                          height="1px"
                          class="mt-0 pb-0"></v-checkbox>
            </v-container>
          </details>
        </v-flex>
      </v-flex>

      <v-flex d-flex xs9>
        <v-container grid-list-lg>

          <v-layout row>
            <v-layout row wrap>
              <v-flex
                v-for="(ad,i) of ads"
                :key="ad.id"
                xs3

              >

                <v-card>
                  <!--                  :src="'../static/'+ad.components[0].name+'.png'"-->
                  <!--                  <v-responsive-->
                  <v-img
                    :src="ad.image"
                    aspect-ratio="2.75"
                    height="200px"
                    contain
                  ></v-img>

                  <v-card-title>
                    <h3 class="headline text-xs-center mb-0">Сервер Талмер {{ad.name}}</h3>
                    <ul style="min-height: 125px">
                      <li>{{ad.pr_count}}</li>
                      <li>Корпус {{ad.corp}}</li>
                      <li>ОЗУ до {{ad.ram}}</li>
                      <li v-for="(fixed,key) in ad.dd['fixed']"
                          :key="ad.id"
                          v-if="fixed != 0">
                        Накопители {{key}} форм-фактора
                      </li>
                      <li v-for="(hot_swap,key) in ad.dd['hot_swap']"
                          :key="ad.id"
                          v-if="hot_swap != 0">
                        Накопители {{key}} форм-фактора
                      </li>
                      <template v-for="(fixed) in ad.lan">
                        <li v-for="(param,key,i) in fixed" v-if="param != 0"> {{key}} - {{param}} шт.</li>
                      </template>
                    </ul>
                  </v-card-title>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-hover>
                      <div slot-scope="{ hover }">
                        <v-btn v-if="!hover" flat class="primary " :to="'/ad/' + ad._id">Заказать</v-btn>
                        <v-btn v-if="hover" flat class="orange lighten-3" :to="'/ad/' + ad._id">Заказать</v-btn>
                      </div>
                    </v-hover>
                  </v-card-actions>

                </v-card>
              </v-flex>
            </v-layout>
          </v-layout>

        </v-container>

      </v-flex>

    </v-layout>
  </v-container>

</template>

<script>
  export default {
    name: "OneCategory",
    props: {
      id: String,
    },
    data() {
      return {
        typeCategory: {
          Rack1U: "1 Unit сервера ТАЛМЕР",
          Rack2U: "2 Unit сервера ТАЛМЕР",
          Rack3U: "3 Unit сервера ТАЛМЕР",
          Rack4U: "4 Unit сервера ТАЛМЕР",
        },
        lineProcArr: '',
        adsVolumeProc: '',
        formFactor: '',
        network: '',
        asdMemorys: '',
      }
    },
    computed: {
      loading() {
        return this.$store.getters.loading
      },
      ads() {
        return this.$store.getters.oneCategory
      },
      filters() {
        return this.$store.getters.filters
      },
      filterAds() {
        {
          let filterAd = this.ads
            .filter(ad => {
              return this.hotSwap.length == 0 || ad.components.some(component => component.attributes.some(attribute =>
                this.hotSwap.includes(attribute.name) && attribute.category == 'Hot-swap'
                && attribute.value == this.hotSwaps['volume'][this.hotSwapObj[attribute.name]]))
            })
            .filter(ad => {
              return this.fixed.length == 0 || ad.components.some(component => component.attributes.some(attribute => this.fixed.includes(attribute.name) &&
                attribute.category == 'Fixed' &&
                attribute.value == (this.fixeds['volume'][this.fixedObj[attribute.name]])));
            })
            .filter(ad => {
              return this.asdMemorys == 0 || ad.components.some(component => component.attributes.some(attribute =>
                attribute.value == this.memorys[this.asdMemorys] && attribute.name == 'Максимальный объем памяти'));
            })
            .filter(ad => {
              return this.network.length == 0 || ad.components.some(component => component.attributes.some(attribute => this.network.includes(attribute.name.split('Кол-во портов ')[1]) &&
                attribute.category.match('Встроенный сетевой интерфейс') &&
                attribute.value != 0));
            })
            .filter(ad => {
              return this.formFactor.length == 0 || ad.components.some(component => component.attributes.some(attribute => this.formFactor.includes(attribute.value) && attribute.name == 'Форм-фактор шасси'));
            })
            .filter(ad => {
              return this.adsVolumeProc.length == 0 || ad.components.some(component => component.attributes.some(attribute => this.adsVolumeProc.includes(attribute.value) && attribute.name == 'Кол-во процессоров'));
            })
            .filter(ad => {
              return this.lineProcArr.length == 0 || ad.components.some(component => component.attributes.some(attribute => attribute.category == 'Процессорный сокет' && this.socket.includes(attribute.value)
              ));
            });
          this.filterAds = filterAd.length;
        }
      }
    },
    created() {
      this.$store.dispatch('getOneCategoryJson', this.id);
    }
  }
</script>

<style scoped>
  summary {
    cursor: pointer;
  }

  details {
    margin-bottom: 10px;
  }

  .orangeHover:hover {
    background: #ffcc80 !important;
    color: black !important;
    transition: background ease-in-out 0.6s !important;
  }

  .orangeHover:not(:hover) {
    transition: background ease-in-out 0.6s !important;
  }
</style>



