import axios from 'axios'

class User {
  constructor(id) {
    this.id = id
  }
}

export default {
  state: {
    email: '',
    userId: null,
    isLoggedIn: false,
    loginError: '',
    user: null
  },
  mutations: {
    logInUser(state, payload) {
      state.email = payload.email;
      state.userId = payload.userId;
      state.isLoggedIn = true
    },
    // setUsernew(state, payload) {
    //   state.email = payload.email;
    //   state.userId = payload.userId;
    //   state.isLoggedIn = true
    // },
    loginError(state) {
      state.isLoggedIn = false;
      state.loginError = 'Email и/или Пароль не корректен. Авторизация УПС. '
    },
    setUser(state, payload) {
      state.user = payload
    }

  },
  actions: {
    async loginUser({commit}, data) {
      commit('clearError');
      commit('setLoading', true);

      try {
        axios.post('http://10.100.0.108:8900/api/login', data)
          .then((response) => {
            console.log(response.data);
            // commit('setUser', response.data);
            commit('setUser', new User(response.data));
            commit('setLoading', false);
          });
      } catch (error) {
        commit('setLoading', false);
        commit('loginError');
        throw error
      }
    },
    async registerUser({commit}, data) {
      commit('clearError');
      commit('setLoading', true);
      // console.log(data);
      try {
        axios.post('http://10.100.0.108:8900/api/register', data)
          .then((response) => {
          console.log(response.data);
          commit('setUsernew', new User(response.data));
          // commit('setUsernew', response.data);
          commit('setLoading', false);
        });
      } catch (error) {
        commit('setLoading', false);
        commit('setError', error.message);
        throw error
      }
    },
    autoLoginUser({commit}, payload) {
      commit('setUser', new User(response.data))
    },
    logoutUser({commit}) {
      try {
        axios.post('http://10.100.0.108:8900/api/logout')
          .then(commit('setUser', null));
      } catch (error) {
        commit('setError', error.message);
        throw error
      }

    }
  },
  getters: {
    isLoggedIn: state => state.isLoggedIn,
    userId: state => state.userId,
    loginError: state => state.loginError,
    isUserLoggedIn(state) {
      return state.user !== null
    },
    user(state) {
      return state.user
    },
  }
}



