export default {
  state: {},
  mutations: {},
  actions: {},
  getters: {
    adByIdMemory() {
      return (ad) => {
        let allMemory = [],
          maxMemory = [];
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == 'Максимальный объем памяти') {
              allMemory.push(attribute.value)
            }
          }
        }
        maxMemory = Math.max.apply(null, allMemory);

        return maxMemory
      }
    },
    satSas() {
      return (ad) => {
        let satSas = [];
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Дисковый контроллер" && attribute.value != 0) {
              if (attribute.name == 'SAS' && attribute.value != 0) {
                satSas.push('SATA')
                satSas.push('SAS')
              } else {
                satSas.push('SATA')
              }
            }
          }
        }
        satSas = [...new Set(satSas)];

        return satSas
      }
    },
    bayUsbCom() {
      return (ad) => {
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Отсек USB/COM" && attribute.value != "-") {
              return +attribute.value
            }
          }
        }
      }
    },
    baySlimDVD() {
      return (ad) => {
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Отсек SlimDVD" && attribute.value != "-") {
              return +attribute.value
            }
          }
        }
      }
    },
    dopBayCount() {
      return (ad) => {
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Отсек 5,25\"") {
              return +attribute.value
            }
          }
        }
      }
    },
    dopBay() {
      return (ad) => {
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Отсек 5,25\"") {
              return +attribute.value
            }
          }
        }
      }
    },
    satDom() {
      return (ad) => {
        let nakopitel = [],
          satDomSas = {};
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == 'SuperDOM' && attribute.value !=0) {
              satDomSas['name'] = 'SuperDOM';
              satDomSas['value'] = attribute.value;
              nakopitel.push(satDomSas);
            }
            satDomSas={};
            if (attribute.name  == 'Кол-во задних отсеков под 2 HDD 2,5" Hot-Swap' && attribute.value !=0) {
              satDomSas['name'] = '2 HDD 2,5\"';
              satDomSas['value'] = attribute.value;
              nakopitel.push(satDomSas);
            }

          }
        }
        nakopitel = [...new Set(nakopitel)];
        console.log(nakopitel);
        return nakopitel
      }
    },
    adSocket() {
      return (ad) => {
        let adSocket = '';
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == "Тип сокета") {
              adSocket = attribute.value
            }
          }
        }
        return adSocket
      }
    },
    countProc() {
      return (ad) => {
        let countProc = '';
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == "Кол-во процессоров") {
              countProc = attribute.value
            }
          }
        }

        return countProc
      }
    },
    satSasDom() {
      return (ad) => {
        let satSas = [];
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Дисковый контроллер" && attribute.value != 0) {
              if (attribute.name == 'SAS' && attribute.value != 0) {
                satSas.push('SATA')
                satSas.push('SAS')
              } else {
                satSas.push('SATA')
              }
            }
          }
        }
        satSas = [...new Set(satSas)];

        return satSas
      }
    },
  }
}



