export default {
  state: {},
  mutations: {},
  actions: {},
  getters: {
    getAttributeTitulText() {
      return (ad, countProc, satSas, formFactors) => {
        let attributeTitulText = {};
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == "Chipset") {
              attributeTitulText['chipset'] = attribute.value.split('chipset')[0]
            }
            if (attribute.name == 'Максимальная частота') {
              attributeTitulText['workFrequency'] = attribute.value
            }
            if (countProc == 1) {
              attributeTitulText['supportProc'] = '1-го процессора'
            } else if (countProc == 2) {
              attributeTitulText['supportProc'] = '2-х процессоров '
            }
            if (satSas.length == 2) {
              attributeTitulText['satSas'] = 'SATA или SAS '
            } else if (satSas.length == 1) {
              attributeTitulText['satSas'] = 'SATA '
            }
            if (formFactors.length == 2) {
              attributeTitulText['formFactors'] =
                'стандартного (3,5”) и/или малого форм-фактора (2,5”) c возможностью «горячей» замены'
            } else if (formFactors.length == 1) {
              attributeTitulText['formFactors'] = 'малого форм-фактора (2,5”) c возможностью «горячей» замены'
            }
          }
        }
        return attributeTitulText
      }
    },
    getMemory() {
      return (ad) => {
        let memory = {};
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == "Тип памяти SDRAM") {
              memory['typeMemory'] = attribute.value
            }
            if (attribute.category == "Характеристики RAM" && attribute.value == 'да') {
              memory['haracteristikMemory'] = attribute.name
            }
            if (attribute.name == "Тип модуля") {
              memory['typeModul'] = attribute.value
            }
          }
        }
        return memory
      }
    },
    getSelectAllFormFactor() {
      return (valueObjFormFactor) => {
        let sum = 0;
        if (isNaN(sum)) {
          sum = 0;
        }
        for (let i in valueObjFormFactor) {
          sum += +valueObjFormFactor[i]['volume' + (valueObjFormFactor[i]['id'])];
        }

        if (isNaN(sum)) {
          sum = 0
        }
        return sum
      }
    },
    getFizPAramCorp() {
      return (ad) => {
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Физические параметры корпуса") {
              return attribute.value.split(' ')[1]

            }
          }
        }
      }
    },
    getMaxSlotMaterinka() {
      return (ad, maxSlotMaterinka) => {
        let count = 0;
        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.name == "PCI-E x 16" || attribute.name == "PCI-E x 8") {
              maxSlotMaterinka += +attribute.value;
              count += +attribute.value

            }
          }
        }
        return count
      }
    },
    getMaxSlotKorpus() {
      return (ad, maxSlotKorpus) => {

        for (let ade in ad.components) {
          let components = ad.components[ade];

          for (let component in components.attributes) {
            let attribute = components.attributes[component];

            if (attribute.category == "Кол-во отсеков под слоты расширения") {
              maxSlotKorpus = +attribute.value;
              return attribute.value
            }
          }
        }
      }
    },
    getVolumeNetwork() {
      return (network,networks) => {
        if (network) {
          for (let i = 0; i < networks.length; i++) {
            for (let name in networks[i]) {
              if (name == network) {
                return networks[i]
              }
            }
          }
        }
        return 0
      }
    }

  }
}



